.Tag {
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 3px;
  color: #333333;
  font-size: 14px;
  padding: 0 5px;
}

.Tag.outline {
  border-color: #cccccc;
}

.Tag.success {
  background-color: #a5cd91;
  border-color: #a5cd91;
  color: #ffffff;
}

.Tag.warning {
  background-color: #e6c887;
  border-color: #e6c887;
  color: #ffffff;
}

.Tag.danger {
  background-color: #d9534f;
  border-color: #d9534f;
  color: #ffffff;
  opacity: 0.7;
}



