.Card {
  background-color: #ffffff;
  border: 1px solid #cccccc;
  border-radius: 5px;
  margin: 40px 0;
  padding: 40px 30px;
  position: relative;
  -webkit-transition: all 0.6s ease-in-out 0s;
  -o-transition: all 0.6s ease-in-out 0s;
  transition: all 0.6s ease-in-out 0s;
}

.Card .before {
  background-color: #82a5d7;
  border-radius: 50%;
  color: #ffffff;
  font-size: 22px;
  font-weight: 300;
  height: 58px;
  left: -20px;
  line-height: 58px;
  position: absolute;
  text-align: center;
  top: -20px;
  width: 58px;
}

.Card .P {
  white-space: pre-line;
}



